.header {
  height: 100px;
  box-shadow: 0px 0px 20px 0px rgb(0, 0, 0, 0.15);
  width: 100vw;
  position: fixed;
  top: 0;
  overflow: hidden;
  z-index: 100000000000000;
  background: white;
}
.container_logo {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 1.2rem;
}

.container_logo > img {
  width: 5rem;
  height: 1.4rem;
}
.icon_menu_header {
  width: 1rem;
  height: 1rem;
}

.container_header_menu > div {
  flex: 1;
  justify-content: center;
  padding: 0.60rem 0;
  cursor: pointer;
}
.container_header_menu > div.active {
  border-bottom: 2px solid rgb(34, 170, 88);
}
