.container__slide > div {
  height: 200px;
}
.container__slide > div > div > div {
  border-radius: 0.25rem;
  padding: 0 0.8rem !important;
  margin: 0 !important;
  border-radius: 0.25rem;
  overflow: hidden;
  cursor: pointer;
}

.container__slide > div > div > div > div > div {
  display: flex !important;
  justify-content: center;
  align-items: center;
}
.container__slide > div > div > div > div > div > img {
  border-radius: 0.25rem;
}


.container__slide > button {
  display: none !important;
}


/* secation */
.container__section > button{
    display: none !important;
}

