@font-face {
  font-family: "yekanBold";
  src: url("./assets/font/IRANYekanBlack.ttf") format("truetype");
}
@font-face {
  font-family: "reglurYekan";
  src: url("./assets/font/IRANYekanRegular.ttf") format("truetype");
}
@font-face {
  font-family: "mediumYekan";
  src: url("./assets/font/IRANYekanMedium.ttf") format("truetype");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  direction: rtl;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 0;
  height: 0;
}
body::-webkit-scrollbar-thumb {
  width: 0;
  height: 0;
}

.yekanBold {
  font-family: yekanBold;
}
.reglurYekan {
  font-family: reglurYekan;
}
.mediumYekan {
  font-family: mediumYekan;
}
.font80 {
  font-size: 0.8rem;
}
.font75 {
  font-size: 0.75rem;
}
.box-shadow {
  box-shadow: 1px 1px 10px 2px rgb(0, 0, 0, 0.15);
}

.textOverFlow{
  text-overflow: ellipsis
}
@tailwind base;
@tailwind components;
@tailwind utilities;
